.main{
    padding: 10px;
    height: 100vh;

}
.layout{
    display: flex;
}
.sidebar{
    min-height: 100%;
    width: 300px;
    border-radius: 5px;
    color: white;
    background-color: rgb(104, 144, 137);
    box-shadow: 0 0 2px grey;
    margin-right: 20px;

}
.content{
    width: 100%;
    height: 100%;
}
.header{
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px grey;
    background-color: white;
}
.body{
    height: 85vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px grey;
    background-color: white;
}
.logo h6{
    text-align: center;
    font-size: 1.5rem;
    margin: 20px 0px;
}

.menu{
    margin-top: 50px;
}
.menu-item{
    margin-top: 30px;
}
.menu-item a{
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}
.menu-item i{
    font-size: 1.2rem;
    margin: 0 15px;
}
.active{
    background-color: white;
    padding: 10px;
}
.active a, .active i{
    color: rgb(51, 1, 1);
}
.header-content{
    margin: 0 15px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 50px;
}
.header-content i{
    margin-right: 20px;
    font-size: 1.2rem;
}
.header-content a{
    font-size: 1.2rem;
    text-decoration: none;
    margin: 0 10px;
    color: red;
    text-transform: capitalize;
    font-weight: bold;
}